___CSS_LOADER_EXPORT___.push([module.id, `:root{
  --primary-color: #0ad7d7;
  --primary-color-dark: #098989;
}

*{
  box-sizing: border-box;
  outline: none;
}

body{
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: 'Open sans', serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container{
  max-width: 640px;
  margin: 50px auto;
  background: #FFF;
  padding: 20px;
  border-radius: 10px;
}

form input, form label{
  display: flex;
}

form button{
  display: block;
}

form input{
  height: 30px;
  width: 30%;
  padding: 0 20px;
  margin-bottom: 20px;
  margin-left: 20px;
}
input[type="checkbox"]{
  width: 20px;
  height: 20px;
}

form input:focus{
  outline: 1px solid var(--primary-color);
}

form button{
  border: none;
  background: var(--primary-color);
  color: white;
  font-size: 18px;
  font-weight: 700;
  height: 40px;
  cursor: pointer;
  margin-top: 20px;
}

form button:hover{
  background: var(--primary-color-dark);
}

.container h1{
  margin-bottom: 50px;
  text-align: center;
}

.container .result{
  color: var(--primary-color);
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 100px;
